.form h4 {
  font-size: 1.4em;
}

.form p {
  font-size: 1em;
}

.form Button {
  margin-left: 1em;
  width: 40%;
  flex-grow: 1;
}

.personalInfo h4 {
  font-size: 1.4em;
}

.personalInfo p {
  font-size: 1.2em;
}

.form Input[type='number']::-webkit-inner-spin-button,
.form Input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.personalInfo Button {
  width: 95%;
  flex-grow: 1;
}
